@import url(https://fonts.googleapis.com/css?family=Roboto:500);

:root{
    --google-blue: #4285f4;
    --button-active-blue: #1669F2;
}

.google-btn {
  width: 184px;
  height: 42px;
  padding:1vw;
  background-color: var(--react-blue);
  border-radius: 15px;
  box-shadow: 0 3px 4px 0 rgba(0,0,0,.25);
  transform: scale(.8);
  margin: 0 auto;
}
  .google-icon-wrapper {
    position: absolute;
    margin-top: 1px;
    margin-left: 1px;
    width: 40px;
    height: 40px;
    border-radius: 2vw;
    background-color: var(--white);
  }
  .google-icon {
    position: absolute;
    margin-top: 11px;
    margin-left: -9px;
    width: 18px;
    height: 18px;
  }
  .btn-text {
    float: right;
    margin: 11px 11px 0 0;
    color: var(--white);
    font-size: 14px;
    letter-spacing: 0.2px;
    font-family: "Roboto";
  }btn-text:hover{cursor:grab}
  .google-btn:hover {
    box-shadow: 0 0 6px var(--google-blue);
    cursor:auto;
  }
  .google-btn:active {
    background: var(--button-active-blue);
  }

.card {
  background-color:var(--white);
  padding: 8vh 8vw;
  display: grid;
  margin: 10vh 0;
  color: var(--react-dark);
  justify-content: center;
  width: 30vw;
  border-radius: 30px;
  box-shadow: 0 1vh 10px -5px var(--dark-shadow);
}
.card h1 {
  font-size: 36pt;
}

#or {
  margin: 2vh 0;
  font-weight: 700;
}

form {
  margin:5vh 0 2vh;
}

.input-name {
  color: var(--react-dark);
  width: 7em;
}

label {
  float: inline-start;
  margin: 0 0 1vh 10px;
  font-size: 12pt;
}

.register-input{
    display:grid;
    justify-content: center;
    border: 2px solid var(--react-blue);
    border-radius: 15px;
    background: var(--white);
    color: var(--react-dark);
    font-size: 12pt;
    padding: 2vh 15px;
    margin: 2vh auto 4vh;
    width: 30vw;
    transition:.2s;
}
.register-input::placeholder {
    color: var(--react-dark);
}
.register-input:focus {
  border: 4px solid var(--react-blue);
  transform: scaleX(1.1);
  box-shadow: 0 1vh 10px -10px var(--dark-shadow);
}

.register-link {
    font-size:.8rem;
    border: none;
    font-weight: 700;
    text-shadow: 0 0 1px var(--react-blue);
    background-color:transparent;
    color: var(--react-blue);
    margin: 0 10px;
}
.register-link:hover {
  color:var(--react-dark);
  transition:.2s;
}

@media (max-width:475px) {
  .google-btn{
    transform: scale(1);
  }
  .card {
    width: 70vw;
  }
  .register-input{
    width: 60vw;
  }
}