@import url(https://fonts.googleapis.com/css?family=Roboto:500);
:root{
  --white-dark: #d8e3e7;
  --white:#fefefe;
  --white-plain:#fafafa;
  --google-blue: #4285f4;
  --blue-light:#51c4d3;
  --blue:#126e82;
  --blue-dark:#132c33;
  --blue-shadow:rgba(97,217,250,.2);
  --dark-shadow: rgba(0,0,0,.3);
  --blue-sea:#0d79d5;
  --react-blue:#20cfff;
  --react-blue-light:#61dafb;
  --react-dark:#282c34;
  --button-active-blue: #1669F2;
}

*{
  max-width: 100vw;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  padding: 0;
  margin: 0;
}

body {
  padding: 0;
  margin: 0;
}

a {
  color: var(--react-blue);
  text-decoration: none;
}
a:hover {
  color: var(--blue-sea);
}

ul {
  margin-left: 10vw;
}

li {
  list-style-type: none;
  /* display: inline-block; */
  float: left;
  padding: 3vh 0;
  margin: 0 25px;
}

.App {
  text-align: center;
}

.App-logo {
  width: 40vw;
  pointer-events: none;
}

.App-header-home .App-logo {
  width: 40vw;
  position: absolute;
  right: 6vw;
  top: 25vh;
  width: 20em;
  pointer-events: none;
}

.App-header {
  padding-top: 2vh;
  background-color: var(--white-plain);
  min-height: 98vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: var(--react-blue);
}

.App-header-home {
  padding-top: 2vh;
  background-color: var(--white-plain);
  min-height: 98vh;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  font-size: calc(10px + 2vmin);
  color: var(--react-blue);
}

.App-link {
  color: #61dafb;
}

.App-nav {
  background-color: transparent;
  color: var(--react-blue);
  position: absolute;
  font-size: 1em;
  display: inline-block;
  top: 0;
  padding: 2vw 0 0;
  left: 0;
  min-height: 3vh;
  width: 100vw;
  font-weight: 700;
}

.blue-button {
  color: var(--white);
  background-color: var(--react-blue);
  border-radius: 20px;
  border-color: transparent;
  padding: 10px 10px;
  font-size: 1em;
  font-weight: 600;
  margin-top: 2vh;
  transition: 0.2s;
  width: 43vw;
}

.blue-button:hover {
  color: var(--white);
  -webkit-transform: scale(.95);
          transform: scale(.95);
  background-color: var(--blue-sea);
  box-shadow: 5px 5px 50px -10px var(--blue-sea);
  transition: 0.2s;
}

.logout-button {
  margin: 4vh 0;
  border: 2px solid var(--react-blue);
  border-radius: 10px;
  padding: 5px 10px;
  font-size: 12pt;
  font-weight: 700;
  background-color: var(--react-blue);
  color:var(--white);
  transition: .2s;
}
.logout-button:hover, .nav-logout-button:hover {
  background-color: var(--white);
  color:var(--react-blue);
  transition: .2s;
}

.Main-logo {
  position: absolute;
  max-width: 30px;
  top: 5.5vh;
  left: 5vw;
}

.nav-logout-button {
  margin: 0 1vw;
  border: 2px solid var(--react-blue);
  border-radius: 10px;
  padding: 5px 10px;
  background-color: var(--react-blue);
  color:var(--white);
  transition: .2s;
}

.opening {
  text-align: left;
  display: inline-block;
  box-shadow: 0 1vh 10px -5px var(--dark-shadow);
  position: static;
  color: var(--react-dark);
  background-color: var(--white);
  border-radius: 20px;
  margin: 15vh 4vw;
  padding: 15vh 40vw 10vh 10vw;
  max-width: 43vw;
}

.opening h1 {
  font-size: 3em;
}

.opening p {
  font-size: .7em;
  font-weight: 300;
  margin: 5vh 0;
}

.user-profile {
  float: right;
  align-items: center;
  height: 3vh;
  padding: 3vh 6vw;
}

.user-profile p {
  display: inline-block;
}

.user-profile-picture {
  border-radius: 50%;
  height: 10vh;
  margin: 5vh;
}

@media (max-width:960px) {
  .App-logo {
    display: none;
  }

  .opening {
    max-width: 70vw;
    padding: 15vh 10vw 10vh 12vw;
  }

  .blue-button {
    width: 72vw;
  }
}

@media (max-width:768px) {
  .Main-logo {
    display: none;
  }
}

@media (max-width:425px) {
  .App-nav img {
    display: none;
  }
  #user-info {
    display: none;
  }
  li {
    margin: 0 15px;
  }
  .opening{
    margin-top: 10vh;
  }
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
:root{
    --google-blue: #4285f4;
    --button-active-blue: #1669F2;
}

.google-btn {
  width: 184px;
  height: 42px;
  padding:1vw;
  background-color: var(--react-blue);
  border-radius: 15px;
  box-shadow: 0 3px 4px 0 rgba(0,0,0,.25);
  -webkit-transform: scale(.8);
          transform: scale(.8);
  margin: 0 auto;
}
  .google-icon-wrapper {
    position: absolute;
    margin-top: 1px;
    margin-left: 1px;
    width: 40px;
    height: 40px;
    border-radius: 2vw;
    background-color: var(--white);
  }
  .google-icon {
    position: absolute;
    margin-top: 11px;
    margin-left: -9px;
    width: 18px;
    height: 18px;
  }
  .btn-text {
    float: right;
    margin: 11px 11px 0 0;
    color: var(--white);
    font-size: 14px;
    letter-spacing: 0.2px;
    font-family: "Roboto";
  }btn-text:hover{cursor:grab}
  .google-btn:hover {
    box-shadow: 0 0 6px #4285f4;
    box-shadow: 0 0 6px var(--google-blue);
    cursor:auto;
  }
  .google-btn:active {
    background: #1669F2;
    background: var(--button-active-blue);
  }

.card {
  background-color:var(--white);
  padding: 8vh 8vw;
  display: grid;
  margin: 10vh 0;
  color: var(--react-dark);
  justify-content: center;
  width: 30vw;
  border-radius: 30px;
  box-shadow: 0 1vh 10px -5px var(--dark-shadow);
}
.card h1 {
  font-size: 36pt;
}

#or {
  margin: 2vh 0;
  font-weight: 700;
}

form {
  margin:5vh 0 2vh;
}

.input-name {
  color: var(--react-dark);
  width: 7em;
}

label {
  float: inline-start;
  margin: 0 0 1vh 10px;
  font-size: 12pt;
}

.register-input{
    display:grid;
    justify-content: center;
    border: 2px solid var(--react-blue);
    border-radius: 15px;
    background: var(--white);
    color: var(--react-dark);
    font-size: 12pt;
    padding: 2vh 15px;
    margin: 2vh auto 4vh;
    width: 30vw;
    transition:.2s;
}
.register-input::-webkit-input-placeholder {
    color: var(--react-dark);
}
.register-input:-ms-input-placeholder {
    color: var(--react-dark);
}
.register-input::placeholder {
    color: var(--react-dark);
}
.register-input:focus {
  border: 4px solid var(--react-blue);
  -webkit-transform: scaleX(1.1);
          transform: scaleX(1.1);
  box-shadow: 0 1vh 10px -10px var(--dark-shadow);
}

.register-link {
    font-size:.8rem;
    border: none;
    font-weight: 700;
    text-shadow: 0 0 1px var(--react-blue);
    background-color:transparent;
    color: var(--react-blue);
    margin: 0 10px;
}
.register-link:hover {
  color:var(--react-dark);
  transition:.2s;
}

@media (max-width:475px) {
  .google-btn{
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  .card {
    width: 70vw;
  }
  .register-input{
    width: 60vw;
  }
}
